.c-toast-container {
  position: fixed;
  display: flex;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 2em;
  overflow: hidden;
  z-index: 9999;
  pointer-events: none;
}
.c-toast-container--top {
  flex-direction: column;
}
.c-toast-container--bottom {
  flex-direction: column-reverse;
}
@media screen and (max-width: 768px) {
  .c-toast-container {
    padding: 0;
    position: fixed !important;
  }
}
